* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
}
ul,
li {
  list-style: none;
}
a {
  text-decoration: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}
.posts {
  display: flex;
  flex-wrap: wrap;
  padding: 15px;
  justify-content: space-between;
}

.post {
  width: 32%;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  padding: 7px;
}
.post h2 {
  text-align: center;
  text-transform: uppercase;
}
.post a {
  width: 100%;
  height: 250px;
  display: block;
  margin: 7px 0;
}
.post a img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  object-position: center;
}

.post p {
  margin-top: 10px;
}
.post .post-detail {
  display: flex;
  align-items: center;
}
.post .post-detail i:last-child {
  margin-left: 10px;
}
.post .post-detail i {
  font-size: 20px;
  cursor: pointer;
}
.post .post-detail i.liked {
  color: red;
}

.single-post-wrapper {
  display: flex;
  align-items: flex-start;
}
.single-post-wrapper img {
  max-width: 350px;
  margin-right: 30px;
}
.comments-wrapper {
  flex-grow: 1;
}
.single-post-wrapper ul {
  display: flex;
  flex-direction: column;
}
.single-post-wrapper ul li {
  flex-direction: column;
  display: flex;
  padding: 5px;
  background-color: #ccc;
  margin-bottom: 10px;
}
.comments-wrapper form {
  display: flex;
  flex-direction: column;
}
.comments-wrapper form *:not(:last-child) {
  margin-bottom: 10px;
}
.comments-wrapper form * {
  height: 35px;
  padding: 5px;
}
@media (max-width: 940px) {
  .post {
    width: 48%;
  }
}
@media (max-width: 600px) {
  .post {
    width: 100%;
  }
}
